function map () {

    ymaps.ready(init);

    function init() {
        //  .    
        let myMap = new ymaps.Map("map", {
            center: [55.78633856896589,37.60752899999993],
            zoom: 17,
            controls: []
        }),
         //      "".
    
     myGeoObject = new ymaps.GeoObject({
        //  .
        geometry: {
            type: "Point",
            coordinates: [55.78633856896589,37.60752899999993]
        },
        // .
        // properties: {
        //     //  .
        //     iconContent: ',  .7,  301',
        //     hintContent: '+7 (495) 737-08-45'
        // }
    }, {
        // .
        //        .
        preset: 'islands#blackStretchyIcon',
        //   .
        // draggable: true
    }),
    myPieChart = new ymaps.Placemark([
        55.847, 37.6
    ], 
     {
        //    .
        iconLayout: 'default#pieChart',
        //    .
        iconPieChartRadius: 30,
        //    .
        iconPieChartCoreRadius: 10,
        //    .
        iconPieChartCoreFillStyle: '#EE042C',
        // C -     .
        iconPieChartStrokeStyle: '#EE042C',
        //  -     .
        iconPieChartStrokeWidth: 3,
        //    .
        iconPieChartCaptionMaxWidth: 200
    });

    myMap.geoObjects
    .add(myGeoObject)
    .add(myPieChart)
    .add(new ymaps.Placemark([55.78633856896589,37.60752899999993], {
        balloonContent: '<strong>,  .7,  301</strong>'
    }, {
        preset: 'islands#icon',
        iconColor: '#EE042C'
    }));
    myMap.behaviors.disable('scrollZoom');
        
    }
    
}
module.exports = map;